<script>
import VerificationCodeModule from '../components/topPage/VerificationCode'
import MusicButton from '../components/topPage/MusicButton'
import { verificationLogic } from '../mixins/topPage/verificationLogic'
import {login} from '../api/login'

export default {
  name: 'Login',
  data() {
      return {
        fit: 'fill',
        validateForm: {
            username: '',
            password: '',
            vertificationCode: ''
        },
        qrDialogVisible: false,
        mailDialogVisible: false,
        duplicateLoginFlag: false,
        left_drawer: false,
        right_drawer: false,
        qrCodeUrl: '',
        loading: null,
        checkName:  (rule, value, callback) => {
        if (value === '') {
          callback(new Error(''));
        }
        else {
          var uPattern = /^[a-zA-Z0-9_-]{4,16}$/
          if(!uPattern.test(value)){
             callback(new Error('416,,,'));
          }
          else{
             callback();
          }
          callback();
        }
      }
      }
  },

  beforeRouteEnter(to, from ,next){
    next( vm => {
      if(!from || from.name === null){
        if(vm.$store.state.setting.playBgm){
          vm.$notify({title: '', type: 'warning', dangerouslyUseHTMLString: true, message: '<i class="el-icon-video-pause"></i>', offset: 50, duration: 8000,})
        }
      }
    })
  },
  
  methods:{
    submitForm: function(){
       if(this.duplicateLoginFlag) return;
       this.duplicateLoginFlag = true
       this.$refs.validateForm.validate(valid => {
         if(valid){
            this.loading = this.$loading({
              lock: true,
              text: '',
              spinner: 'el-icon-loading',
              background: 'rgba(255, 255, 255, 0.7)'
            })
            login({username: this.validateForm.username, password: this.validateForm.password })
            .then( ( res ) => {
              if(res.account){
                this.$router.push({name: 'ChatRoom'})
                this.$store.dispatch('initialization', res.account)
              }
              else{
                this.loading.close()
              }
            })
            .catch( () =>{
              this.loading.close()
            })
            .finally( ()=> {
                this.duplicateLoginFlag = false
            })
         }
         else{
            this.$message.error('');
            this.duplicateLoginFlag = false
         }
       })
       this.refreshCode()
    },

    goToRgister: function(){
       this.$router.push({name: 'Register'})
    },

    directToGit: function(){
      window.open('https://github.com/shurintou/journey-to-the-west')
    },

    openDialog: function(which){
      this.qrDialogVisible = true
      if(which === 'wechat'){
        this.qrCodeUrl = require('@/assets/images/wechat-qr-code-min.png')
      }
      else if(which === 'line'){
        this.qrCodeUrl = require('@/assets/images/line-qr-code-min.png')
      }
    },

    enterLogin: function(e){
            if(e.keyCode === 13)this.submitForm()
    },
  },

  components:{
      VerificationCodeModule,
      MusicButton,
  },

  mixins:[ verificationLogic ]
}
</script>

